import { createStore } from "vuex";

export default createStore({
  state: {
    activeContent: "",
    websiteLang: document.querySelector("html").getAttribute("lang"),
    qa: {
      "Who is this for ?":
        "It is for people that studied these technologies and want to see them again.",
      "Will this make me a pro ?":
        "Nothing makes you a pro unless you have the passion to study from multi sources.",
      "Will this ever support Arabic ?": "Yes.",
      "Where did I study to make all these ?":
        "Elzero web school, Codevolution and some other sources.",
      "When will we see more content ?":
        "Honestly Idk that may take some time.",
      "Is this any related to BlueMoon ?":
        "The content is the same but the template is brand new. Consider it a rebranding.",
      "How can you support ?":
        "Feedbacks and following me on social media will encourage me to introduce more.",
    },
    news: {
      2: "Added What's new section",
      3: "A lot of under the hood improvements",
    },
    Content: {},
  },
  mutations: {
    changeContent: (state) => {
      state.activeContent = location.href.slice(
        location.href.lastIndexOf("/") + 1
      );
    },
  },
  actions: {
    getContent: (context) => {
      fetch("./content.json")
        .then((response) => response.json())
        .then((l) => (context.state.Content = l))
        .catch((err) => alert(err));
    },
  },
  modules: {},
});
