      <button id="drop" @click="showNav()">
        <i class="fas fa-bars fa-2x"></i>
      </button>
    </div>
    <transition name="fade" mode="out-in">
      <nav v-if="isNavShown">
        <div></div>
        <div class="for-spans">
          <span>
            <router-link :to="`/${lang}/html`">
              <i class="fab fa-html5 fa-2x"></i>
            </router-link>
          </span>
          <span class="not-finished">
