<script>
export default {
  name: "TheHeader",
  data() {
    return {
      isNavShown: false,
      lang: this.$store.state.websiteLang,
    };
  },
  methods: {
    darkMode() {
      document.body.classList.toggle("dark");
      document
        .querySelector("#theme-switch span")
        .classList.toggle("theme-switched");
    },
    showNav() {
      if (this.isNavShown === true) {
        this.isNavShown = false;
      } else {
        this.isNavShown = true;
      }
    },
    visitMe() {
      let result = window.confirm("Continue to Moneer AlDakkak's Portfolio ?");
      if (result === true) {
        window.open(
          "https://moneeraldakkak.github.io/Portfolio-V2/",
          "_blank",
          "true"
        );
      } else {
        return 0;
      }
    },
    changeContent() {
      this.$store.commit("changeContent");
      console.log("hi", this.$store.state.activeContent);
    },
  },
};
</script>

