import { createRouter, createWebHashHistory } from "vue-router";
import ZHome from "../views/Home.vue";

const routes = [
  {
    path: "/",
    name: "ZHome",
    component: ZHome,
  },
  {
    path: "/en/html",
    name: "html",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/en/html.vue"),
  },
  {
    path: "/en/js",
    name: "js",
    component: () => import("../views/en/js.vue"),
  },
  {
    path: "/en/css",
    name: "css",
    component: () => import("../views/en/css.vue"),
  },
  {
    path: "/en/sass",
    name: "css",
    component: () => import("../views/en/sass.vue"),
  },
  {
    path: "/en/vue",
    name: "css",
    component: () => import("../views/en/vue.vue"),
  },
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
});

export default router;
